@import url("https://fonts.googleapis.com/css?family=Poppins&display=swap");
.App {
  text-align: center;
  font-family: "Poppins", sans-serif;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.content-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  
}

h1 {
  font-size: 4rem;
}

button {
  margin-top: 15px;
  padding: 15px 25px;
  font-size: 2rem;
  cursor: pointer;
  outline: none;
  background-color: rgb(255, 251, 11);
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}

button:hover {
  background-color: rgb(218, 214, 5);
}

button:active {
  box-shadow: 0 5px #666;
}

h2 {
  font-size: 3rem;
}

.extracted {
  font-size: 10rem;
}

img {
  position: fixed;
  width: 600px;
  z-index: -1;
  top: 50%;
  left: 50%;
}